import './item.css';

import React,{Component} from 'react';
import {Card, Icon, Avatar, Spin, Modal, Badge} from 'antd';
const { confirm } = Modal;

const { Meta } = Card;

const itemAction = {
  delete: 'delete',
  select: 'select',
  edit: 'edit'
}
export default  class ConfigItem extends Component{
  restartConfirm = () => {
    confirm({
      title: '切换配置',
      content: '代理服务器运行中，需要换到当前配置吗？',
      okText: '好的',
      okType: 'danger',
      cancelText: '取消切换',
      onOk: () => {
        this.selectConfigConfirm()
      },
      onCancel() {
        console.log('Cancel');
      },
    });
  }
  
  selectCurItem = () => {
    this.selectConfigConfirm();
  }
  selectConfigConfirm = () => {
    this.props.selectConfig(this.props.data)
  
  }
  editCurItem = () => {
    this.props.editConfig(this.props.data);
  }
  doAction = (action) => {
    switch (action) {
      case itemAction.select:
        this.selectCurItem();
        break;
      case itemAction.edit:
        this.editCurItem();
        break;
      default:
        console.log('没有相应的action', action)
    }
  }
  turnOffProxyServer = () => {
    this.props.stopCurConfig && this.props.stopCurConfig()
  }
  render(){
    const {data} = this.props;
    return (
      <Spin spinning={this.props.runing} tip={<div onClick={this.turnOffProxyServer}>代理中,点击可停止... </div>}>
        <div>
          <Card
            title={<div>
              <Badge count={this.props.itemIndex + 1} style={{ backgroundColor: '#52c41a' }} />
              {data.fileData.description}</div>}
            actions={[
              <div className="icon-container" onClick={() => {this.doAction(itemAction.select)}}>
                <Icon type="heart" key="heart"/>
                <span>选择</span>
              </div>,
              <div className="icon-container" onClick={() => {this.doAction(itemAction.edit)}}>
                <Icon type="edit" key="edit"/>
                <span>修改</span>
              </div>,
              <div className="icon-container">
                <Icon type="delete" key="delete"/>
                <span>删除</span>
              </div>,
            ]}
          >
            <Meta
              description={<div>
                {data.fileData.description ? data.fileData.description : `${data.fileName}无描述`}</div>}
            />
          </Card>
        </div>
      </Spin>
      
    )
  }
}
