
import React, {Component} from 'react';
import './App.css';
import { message,Alert,notification ,Divider,Collapse,Popover} from 'antd';
import ConfigItem from './configItem';
const crt = require("./rootCA.crt");
const { Panel } = Collapse;



const actions = {
  'FETCH_ALL_CONFIGS': 'getAllConfig',
  'SELECT_CONFIG': 'select_config',
  'EDIT_CONFIG': 'edit_config',
  'START_CALLBACK':'start_callback',
  'GET_CURRENT_CONFIG': 'get_current_config',
  'CHECK_UPDATE': 'CHECK_UPDATE',
  'STOP_CONFIG': 'STOP_CONFIG',
  'GET_PAC_ADDRESS': 'GET_PAC_ADDRESS'
}


class App extends Component {
  state = {
    configs: [],
    startUpSuc: false,
    runningConfigsName: [],
    connectError: false,
    pacAddress: ''
  }
  selectConfig = (data) => {
    const params = {
      action: actions.SELECT_CONFIG,
      payload: data
    }
    this.ws.send(JSON.stringify(params))
  }
  editConfig = (data) => {
    const params = {
      action: actions.EDIT_CONFIG,
      payload: data
    }
    this.ws.send(JSON.stringify(params))
  }
  showMsg = (status, info) => {
    message[status](info)
  }
  getPacAddress = () => {
    this.ws.send(JSON.stringify({action: actions.GET_PAC_ADDRESS}));
  }
  processStartUp = (payload) => {
    this.showMsg(payload.startSuc ? 'success' : 'error', payload.msg);
    this.setState({startUpSuc: payload.startSuc}, () => {
      // !this.state.startUpSuc && this.restartConfirm()
      if (this.state.startUpSuc) {
        this.setState({runningConfigsName: payload.fileName});
        this.getPacAddress();
      }
    });
    //获取所有启动项目
    this.ws.send(JSON.stringify({action: actions.GET_CURRENT_CONFIG}));
  }
  setCurrentConfig = (payload) => {
    // debugger
    const fileNames = payload.map(v => v.fileName);
    this.setState({runningConfigsName: fileNames});
    if (fileNames.length === 0) {
      this.ws.send(JSON.stringify({action: actions.GET_PAC_ADDRESS}));
    }
  }
  setPacAddress = (pacAddress) => {
    this.setState({pacAddress})
  }
  setSelectedItemData = (data) => {
    this.setState({curItemConfig: data})
  }
  versionCompare = ({latestVersion,localVersion}) => {
    if (latestVersion !== localVersion) {
      const text = `检测到新版本${latestVersion},当前版本为${localVersion},请升级获得更多代理规则支持 + 错误修复`
      this.showUpdateTip(text)
    }
  }
  getCurConfig = () => {
    this.ws.send(JSON.stringify({action: actions.FETCH_ALL_CONFIGS}));
    this.ws.send(JSON.stringify({action: actions.GET_CURRENT_CONFIG}));
  
  }
  componentDidMount() {
    //发送请求获取配置信息
    let ws = new WebSocket("ws://localhost:9876");
    this.ws = ws;
    
    ws.addEventListener("open",(evt) => {
      console.log("Connection open ...");
      // ws.send("Hello WebSockets!");
      this.setState({connectError: false});
      this.getCurConfig();
      this.checkUpdate();
    })
  
  
    ws.addEventListener("message",(evt) => {
      console.log( "Received Message: " , evt.data);
      // ws.close();
      let result ;
      try {
        result = JSON.parse(evt.data);
      } catch (e) {
        console.warn('传递数据无法json化，格式错误');
        return
      }
      if (result.action === actions.FETCH_ALL_CONFIGS) {
        this.setState({configs: result.payload});
      }
      if (result.action === actions.START_CALLBACK) {
        const {payload} = result;
        this.processStartUp(payload)
      }
      if (result.action === actions.GET_CURRENT_CONFIG) {
        const {payload} = result;
        payload && this.setCurrentConfig(payload);
        this.getPacAddress();
      }
      if (result.action === actions.GET_PAC_ADDRESS) {
        const {payload} = result;
        payload && this.setPacAddress(payload)
      }
      if (result.action === actions.EDIT_CONFIG) {
        const {payload} = result;
        payload && this.setSelectedItemData(payload)
      }
      if (result.action === actions.CHECK_UPDATE) {
        const {payload} = result;
        payload && this.versionCompare(payload)
      }
      
    })
  
    ws.addEventListener('close', (evt) => {
      console.log("Connection closed.");
      this.setState({connectError: true});
      setTimeout(() => {
        window.location.reload()
      }, 10000)
    });
  }
  checkUpdate = (data) => {
    const params = {
      action: actions.CHECK_UPDATE,
      payload: data
    }
    this.ws.send(JSON.stringify(params))
  }
  showUpdateTip = (text) => {
    notification['info']({
      message: 'b-proxy-cli 检测到新版本',
      duration:null,
      description:text
    });
  
  }
  stopConfig = (fileName) => {
    const params = {
      action: actions.STOP_CONFIG,
      payload: fileName
    }
    this.ws.send(JSON.stringify(params));
    // setTimeout(this.getCurConfig,1000)
  }
  
  render(){
  
    return (
      <div className="App">
        
        {this.state.connectError && (
          <Alert
            message="与代理服务器的通信失败"
            description="代理服务器可能没有启动,10s后网页会刷新重试..."
            type="error"
            showIcon
          />
        )}
          <div className={'config-container'}>
            {this.state.configs.map((v,index) => {
              return (
                <ConfigItem
                  selectConfig={this.selectConfig}
                  editConfig={this.editConfig}
                  stopCurConfig={()=>{this.stopConfig(v.fileName)}}
                  data = {v}
                  proxyIsRunning={this.state.runningConfigsName.length !== 0}
                  runing={this.state.runningConfigsName.includes(v.fileName)}
                  key={index}
                  itemIndex={index}
                />
              )
            })}
          </div>
        <Divider/>
  
        <div className={'footer'}>
          <a href={crt} target={"_blank"} download>点击下载根证书</a>
          <div>
            <p>            windows下配置信任根证书：<a target={"_blank"} href="https://jingyan.baidu.com/article/9f7e7ec0c1107c6f29155461.html">点击查看</a></p>
            <p>
              mac下配置信任根证书：<a target={"_blank"} href="https://blog.51cto.com/viming/2161919">点击查看</a>

            </p>
          </div>
          {!this.state.connectError && (
            <div>
              <Popover content={<div><img src={require("./imgs/pac-tip.png")} alt=""/></div>} title="在mac中配置自动代理链接">
                  <span>
                    自动代理配置url：{this.state.pacAddress}
                  </span>
              </Popover>

            </div>
          )}
          <Divider/>
          <Collapse accordion>
            <Panel header="如何备份我的配置" key="1">
              <p>组件升级的时候，会覆盖安装组件，覆盖配置文件，为了保存你的配置文件，请拷贝配置文件到其他地方，当更新后，拷贝回来即可</p>
            </Panel>
          </Collapse>
        </div>
      </div>
    );
  }
}

export default App;
